.service-card{
    width: fit-content;
    height: 100%;
    box-shadow: 0 0 5px black;
    padding: 10px;
    border-radius: 20px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.service-card a {
    text-decoration: none;
    color: black;
}

.service-card button{
    background-color: white;
    padding: 10px;
    border-radius: 10px;

}